/*** Generic/Common styles for Arth page ***/
.padding-none {
	padding: 0; }

.margin-none {
	margin: 0; }

.dis-flex-center {
  display: flex;
  justify-content: center; }

.arth-section {
  // margin: 120px auto 0px
  text-align: center;
  font-style: normal;
  font-family: $font-family-heading;
  display: flex;
  justify-content: center;
  min-height: 150px;
  width: 100%;
  height: 100%;
  margin-bottom: 20px;
  padding: 30px; }

.arth-heading {
  color: #FFFFFF;
  font-size: 46px;
  font-weight: 700;
  line-height: 55px;
  width: 100%;

  @media screen and (max-width: 500px) {
    font-size: 24px;
    line-height: 18px; }

  &.arth-heading-regular {
    font-size: 33px;

    @media screen and (max-width: 500px) {
      font-size: 25px;
      line-height: 30px; } } }

.arth-para {
  color: #A2A2A2;
  font-weight: normal;
  line-height: 26px;
  padding: 15px 20%;

  &.arth-para-regular {
    font-size: 18px;

    @media screen and (max-width: 500px) {
      font-size: 15px;
      padding: 15px 0; } }

  &.arth-para-medium {
    font-size: 21px;

    @media screen and (max-width: 500px) {
      font-size: 15px;
      padding: 15px 0; } } }


