      60% {
          transform: scale(1.1);
          opacity: .3; }


      80% {
          transform: scale(1.2);
          opacity: .1; }


      100% {
          transform: scale(1.3);
          opacity: 0; } }


@keyframes pulsejg3 {

      0% {
          transform: scale(.6);
          opacity: 0; }


      40% {
          transform: scale(1);
          opacity: .05; }


